<template>
  <div>
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb bg-transparent border border-bottom">
        <li class="breadcrumb-item">
          <router-link to="/" class="text-muted"></router-link>
        </li>
        <li class="breadcrumb-item active text-dark" aria-current="page"></li>
      </ol>
    </nav>
    <div class="container-fluid">
      <div class="row">
      <div class="col-sm-4">
        <div class="form-group">
          <select class="form-control rounded-0" v-model="selectlocation" @change="PenTo(selectlocation)">
            <option value="" disabled></option>
            <option v-for="(item, key) in data" :key="key" :value="item">{{ item[""] }}</option>
          </select>
        </div>
        <div>
          <h5 class="font-weight-bold">
            {{ selectlocation[""] }}
          </h5>
          <small class="text-muted small-text">{{ selectlocation[""] }}</small>
          <br>
          <h5 class="mt-3" v-if="selectlocation.decription!==''">{{ selectlocation.decription }}</h5>
          <img :src="selectlocation.image" :alt="selectlocation['']" class="img-thumbnail rounded-0" v-if="selectlocation.image!==''">
        </div>
      </div>
      <div class="col-sm-8">
        <div id="map" class="map-setting"></div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import L from 'leaflet'
let osmMap = {}
const storeTile = ['Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ']
const storeImage = ['https://i.postimg.cc/FHQ5LrTf/location1.jpg', 'https://i.postimg.cc/Kc6v0y9b/location2.jpg', 'https://i.postimg.cc/C1ZS4vkd/location3.jpg', 'https://i.postimg.cc/sXZCWMCh/location4.jpg',
  'https://i.postimg.cc/wBCbSFn1/location5.jpg', 'https://i.postimg.cc/L4Cj5h6T/location6.jpg', 'https://i.postimg.cc/SRJ2C7NG/location7.jpg', 'https://i.postimg.cc/d0wh4KV3/location8.jpg']
const storedecription = ['', '', '', '', '', '',
  '', '']

export default {
  name: 'LocationInfo',
  data () {
    return {
      product_length: 0,
      data: [],
      selectlocation: '',
      isLoading: false
    }
  },
  methods: {
    CounterCoupute (cartTotalLength) {
      this.product_length = cartTotalLength
    },
    updateMap () {
      this.data.forEach(item => {
        L.marker([item[''], item['']]).addTo(osmMap).bindPopup(`<strong>:${item['']}</strong><br>
          <strong>:<a href="https://www.google.com.tw/maps/place/${item['']}" target="_blank">${item['']}</a></strong><br>
          <strong>:${item['']}</strong>`)
      })
      osmMap.panTo(new L.LatLng(24.130993, 120.721122))
      L.marker([24.130993, 120.721122]).addTo(osmMap).bindPopup(`<strong>:Berserker </strong><br>
          <strong>:<a href="https://www.google.com.tw/maps/place/99" target="_blank">99</a></strong><br>
          <strong>:04-22289111#51311</strong>`).openPopup()
      this.$store.state.isLoading = false
    },
    PenTo (location) {
      osmMap.panTo(new L.LatLng(location[''], location['']))
      L.marker([location[''], location['']]).addTo(osmMap).bindPopup(`<strong>:${location['']}</strong><br>
          <strong>:<a href="https://www.google.com.tw/maps/place/${location['']}" target="_blank">${location['']}</a></strong><br>
          <strong>:${location['']}</strong>`).openPopup()
    }
  },
  mounted () {
    const vm = this
    vm.axios.defaults.withCredentials = false
    const api = 'https://datacenter.taichung.gov.tw/swagger/OpenData/8f477165-6874-4856-b60d-38029958e723'
    vm.$http.get(api).then((Response) => {
      vm.$store.state.isLoading = true
      vm.data = Response.data
      for (let i = 0; i !== vm.data.length; i++) {
        vm.data[i].image = storeImage[i]
        vm.data[i][''] = storeTile[i]
        vm.data[i].decription = storedecription[i]
      }
      vm.updateMap()
    })
    osmMap = L.map('map', {
      center: [24.130993, 120.721122],
      zoom: 10
    })
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '<a target="_blank" href="https://www.openstreetmap.org/"> OpenStreetMap </a>',
      maxZoom: 18
    }).addTo(osmMap)
  },
  create () {
    this.getCart()
  }
}
</script>

<style scoped>
#map {
  height: 100vh;
}
.small-text{
  font-size: 14px;
}
.map-setting {
  z-index: 2;
}
</style>
